// lib/api/client.ts
import axios, { type InternalAxiosRequestConfig } from 'axios'

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000/api'

export const apiClient = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: false, // On utilise Authorization: Bearer
})

// Intercepteur pour ajouter le token
apiClient.interceptors.request.use(
  (config: InternalAxiosRequestConfig) => {
  const token = typeof window !== 'undefined' ? localStorage.getItem('access_token') : null
    if (token && config.headers) {
      config.headers.Authorization = `Bearer ${token}`
    }
    if (process.env.NODE_ENV !== 'production') {
      console.log('üì§ Requ√™te:', config.method?.toUpperCase(), config.url, config.headers.Authorization ? '‚úÖ Token pr√©sent' : '‚ùå Pas de token')
    }
    return config
  },
  (error) => {
    if (process.env.NODE_ENV !== 'production') {
      console.error('‚ùå Erreur intercepteur request:', error)
    }
    return Promise.reject(error)
  }
)

// Intercepteur pour refresh token
apiClient.interceptors.response.use(
  (response) => {
    if (process.env.NODE_ENV !== 'production') {
      console.log('üì• R√©ponse:', response.config.url, response.status)
    }
    return response
  },
  async (error) => {
    const originalRequest = error.config

    if (process.env.NODE_ENV !== 'production') {
      console.error('‚ùå Erreur API:', error.response?.status, error.config?.url)
    }

    if (error.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true
      console.log('üîÑ Tentative de refresh token...')

      try {
        const refreshToken = typeof window !== 'undefined' ? localStorage.getItem('refresh_token') : null
        
        if (!refreshToken) {
          throw new Error('Pas de refresh token')
        }

        const response = await axios.post(`${API_BASE_URL}/token/refresh/`, {
          refresh: refreshToken,
        })

        const { access } = response.data
        if (typeof window !== 'undefined') {
          localStorage.setItem('access_token', access)
        }
        console.log('‚úÖ Token refresh√© avec succ√®s')

        originalRequest.headers.Authorization = `Bearer ${access}`
        return apiClient(originalRequest)
      } catch (refreshError) {
        console.error('‚ùå √âchec du refresh token:', refreshError)
        localStorage.removeItem('access_token')
        localStorage.removeItem('refresh_token')
        
        // Ne rediriger que si on est c√¥t√© client
        if (typeof window !== 'undefined') {
          window.location.href = '/login'
        }
        
        return Promise.reject(refreshError)
      }
    }

    return Promise.reject(error)
  }
)