// lib/api/auth.service.ts
import { apiClient } from './client'
import { Utilisateur } from '@/lib/types'

export interface LoginCredentials {
  email: string
  password: string
}

export interface RegisterData {
  email: string
  password: string
  nom: string
  prenoms: string
  telephone?: string
  langue?: string
}

export interface AuthResponse {
  access: string
  refresh: string
}

export const authService = {
  async login(credentials: LoginCredentials): Promise<Utilisateur> {
    console.log('üîê Tentative de connexion...', credentials.email)
    
    // 1. Obtenir les tokens (backend: POST /login/)
    const tokenResponse = await apiClient.post<{
      access: string
      refresh: string
      user?: Partial<Utilisateur>
    }>('/login/', credentials)
    const { access, refresh } = tokenResponse.data
    
    console.log('‚úÖ Tokens re√ßus')
    
    // 2. Stocker les tokens
    if (typeof window !== 'undefined') {
      localStorage.setItem('access_token', access)
      localStorage.setItem('refresh_token', refresh)
    }
    
    // 3. R√©cup√©rer le profil utilisateur avec le nouveau token
    const userResponse = await apiClient.get<Utilisateur>('/profil/')
    console.log('‚úÖ Profil utilisateur r√©cup√©r√©:', userResponse.data)
    
    return userResponse.data
  },

  async register(data: RegisterData): Promise<{ message: string; user_id: string }> {
    const response = await apiClient.post('/register/', data)
    return response.data as { message: string; user_id: string }
  },

  async verifyEmail(token: string): Promise<{ message: string }> {
    const response = await apiClient.get(`/verify-email/${token}/`)
    return response.data
  },

  async requestPasswordReset(email: string): Promise<{ message: string }> {
    const response = await apiClient.post('/password-reset/request/', { email })
    return response.data
  },

  async confirmPasswordReset(token: string, password: string): Promise<{ message: string }> {
    const response = await apiClient.post('/password-reset/confirm/', { token, password })
    return response.data
  },

  async getCurrentUser(): Promise<Utilisateur> {
    const response = await apiClient.get<Utilisateur>('/profil/')
    return response.data
  },

  async logout(): Promise<void> {
    if (typeof window !== 'undefined') {
      localStorage.removeItem('access_token')
      localStorage.removeItem('refresh_token')
    }
  },
}